import TabReport from '../page/report/index'

const report = [
  {
    path: '/',
    component: TabReport,
    redirect: '/marketing',
    children: [
      {
        path: '/',
        component: r =>
          require.ensure([], () => r(require('../page/report/dataReport')))
      },
      {
        path: 'dataReport',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/report/dataReport')),
            'dataReport'
          )
      },
      {
        path: 'peopleAnalysis',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/report/peopleAnalysis')),
            'warning'
          )
      },
    ]
  }
]

export default report



