<template>
  <TabNavigation :tabItems="tabItems"></TabNavigation>
</template>

<script>
import TabNavigation from '../../components/TabNavigation'
export default {
  // 组件
  components: {
    TabNavigation
  },
  data() {
    return {
      tabItems: {
        activeName: 'warning',
        tabHost: '/marketing/#/setting/',
        items: [
          // { label: '预警管理', name: 'warning' },
          { label: '标的物', name: 'product' },
          { label: '账号管理', name: 'accountmanage' },
          { label: '定向管理', name: 'targetingmanage' },
          { label: '人群管理', name: 'audience' }
        ]
      }
    }
  },
  created: function() {
    //console.log('created:', this.$route.path)
    // 判断是否有
    var that = this
    var list = this.tabItems.items
    var path = (this.$route.path + '/').toLowerCase()
    for (var k in list) {
      var name = list[k]['name']
      if (path.indexOf('/setting/' + name.toLowerCase() + '/') == 0) {
        that.tabItems.activeName = name
        break
      }
    }
  },
  watch: {
    $route(to, from) {
      var that = this
      var list = this.tabItems.items
      var path = (to.path + '/').toLowerCase()
      for (var k in list) {
        var name = list[k]['name']
        if (path.indexOf('/advertising/' + name.toLowerCase() + '/') == 0) {
          that.tabItems.activeName = name
          break
        }
      }
    }
  }
}
</script>


<style scoped>
.tab-container-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.tab-title-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  box-sizing: border-box;
}
.tab-content-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 42px;
  right: 0;
  bottom: 0;
  width: 100%;
}
</style>



