/* Action提交的是 mutation */
const actions = {
  set_accountPage_action(state, parmes) {
    state.commit('SET_ACCOUNTPAGE', parmes)
  },
  // 修改账户
  set_account_action(state, parmes) {
    //console.log('parmes', parmes)
    state.commit('SET_ACCOUNT', parmes)
  }
}

export default actions



