import TabSetting from '../page/setting/index'

const setting = [
  {
    path: '/',
    component: TabSetting,
    redirect: '/marketing',
    children: [
      {
        path: '/',
        component: r =>
          require.ensure([], () => r(require('../page/setting/product')))
          // require.ensure([], () => r(require('../page/setting/warning')))
      },
      {
        path: 'warning',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/warning')),
            'warning'
          )
      },
      {
        path: 'product',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/product')),
            'product'
          )
      },
      {
        path: 'product/new',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/newProduct')),
            'newProduct'
          )
      },
      {
        path: 'accountmanage',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/accountmanage')),
            'accountmanage'
          )
      },
      {
        path: 'accountmanage/new',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/newAccount')),
            'newAccount'
          )
      },
      {
        path: 'accountmanage/edit/:id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/editAccount')),
            'editAccount'
          )
      },
      {
        path: 'targetingmanage',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/targetingmanage')),
            'targetingmanage'
          )
      },
      {
        path: 'audience',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/audience')),
            'audience'
          )
      },
      {
        path: 'audience/file',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/audienceFile')),
            'audienceFile'
          )
      },
      {
        path: 'audience/ad',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/audienceAd')),
            'audienceAd'
          )
      },
      {
        path: 'audience/lookalike',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/setting/audienceLookalike')),
            'audienceLookalike'
          )
      }
    ]
  }
]

export default setting



