const getters = {
  GET_ACCOUNTPAGE(state) {
    return state.accountPage
  },
  getABC(state, n) {
    return 123
  },
  // 获取账户信息
  get_account_action(state) {
    //console.log('state.user:', state.user)
    return state.user.account
  }
}

export default getters



