<template>
  <div class="nav-wrap">
    <el-header style="text-align: right; font-size: 12px">
      <div class="logo"><img src="../../assets/ad_logo.png" alt="logo" /></div>
      <div class="system-wrap">
        <el-dropdown>
          <div class="dropdown-name">{{system}}
            <i class="el-icon-caret-bottom"></i>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="(item, key) in modules.module" :command="key" :key="key">
              <a class="dropdown-a" :href="item.url">{{item.name }}</a>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="account-wrap">
        <div class="account-text">当前账号：</div>
        <el-select v-model="setAccount" @change="changeAccounts" placeholder="请选择" filterable>
          <el-option v-for="item in accounts" :key="item.account_id" :label="item.account_name" :value="item.account_id">
          </el-option>
        </el-select>
      </div>
      <el-dropdown>
        <div class="dropdown-name">{{modules.user_name}}
          <i class="el-icon-caret-bottom"></i>
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>修改密码</el-dropdown-item>
          <el-dropdown-item>
            <a class="dropdown-a" href="/Admin/Login/index">注销</a>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
  </div>
</template>

<script>
import bus from '../../util/bus'

export default {
  created: function() {
    this.getAccounts()
    this.getModule()
    var that = this
    bus.$on('updataAccounts', function(data) {
      //console.log('updataAccounts')
      that.getAccounts()
    })
  },
  data() {
    return {
      system: 'Marketing API',
      accounts: [],
      modules: {}
    }
  },
  methods: {
    // 获取系统列表
    getModule() {
      var that = this
      this.$axios
        .get('/Admin/Home/getModule', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.modules = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          console.log('获取系统列表:', err)
        })
    },

    // 账号请求
    getAccounts() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Account/get', {
          params: {
            state: 'ON'
          }
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if(respData.data.length == 0){
              that.$message('无授权用户，请到账号管理页面')
              setTimeout(function() {
                that.$router.push('/setting/accountmanage')
              }, 500)
            }else{
              that.accounts = respData.data
              that.changeAccounts(respData.data[0]['account_id'])
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    // 更改账号
    changeAccounts(accountId) {
      this.defaultAccountsId = accountId
      var accounts = this.accounts
      for (var index in accounts) {
        if (accounts[index]['account_id'] == accountId) {
          //console.log(accounts[index])
          this.$store.dispatch('set_account_action', accounts[index])
          break
        }
      }
    }
  },
  computed: {
    // 账户修改
    setAccount: {
      get() {
        var account = this.$store.getters.get_account_action
        //console.log('computed:', account)
        return account.account_id
      },
      set(accountId) {
        this.changeAccounts(accountId)
      }
    }
  }
}
</script>


<style scoped>
.nav-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  height: 60px;
  background: #303133;
  color: #fff;
  font-size: 16px;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.dropdown-name {
  color: #fff;
  font-size: 16px;
  margin-right: 36px;
  height: 60px;
  line-height: 60px;
}
.system-wrap {
  float: left;
  height: 60px;
  line-height: 60px;
  margin-left: 20px;
}

.account-wrap {
  float: left;
  height: 60px;
  line-height: 60px;
  margin-left: 20px;
}
.account-wrap .account-text {
  float: left;
  line-height: 60px;
  padding: 0 10px;
  font-size: 16px;
}

.logo {
  height: 60px;
  float: left;
  margin-left: -14px;
  display: inline-block;
}
.logo img {
  width: 50px;
  height: 50px;
  margin-top: 5px;
}

.dropdown-a {
  list-style: none;
  line-height: 36px;
  padding: 0;
  margin: 0;
  font-size: 14px;
  color: #606266;
  cursor: pointer;
  outline: 0;
  text-decoration: none;
}
</style>



