import TabAdvertising from '../page/advertising/index'

const advertising = [
  {
    path: '/',
    component: TabAdvertising,
    redirect: '/marketing',
    children: [
      {
        path: '/',
        component: r =>
          require.ensure([], () => r(require('../page/advertising/campaign')))
      },
      {
        path: 'campaign',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/campaign')),
            'campaign'
          )
      },
      {
        path: 'campaign/new',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/newCampaign')),
            'newCampaign'
          )
      },
      {
        path: 'campaign/edit/:campaign_id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/editCampaign')),
            'editCampaign'
          )
      },
      {
        path: 'campaign/copy/:campaign_id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/newCampaign')),
            'newCampaign'
          )
      },
      {
        path: 'adGroup',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/adGroup')),
            'adGroup'
          )
      },
      {
        path: 'adGroup/sub/:campaign_id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/adGroup')),
            'subAdGroup'
          )
      },
      {
        path: 'adGroup/new',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/newAdGroup')),
            'newAdGroup'
          )
      },
      {
        path: 'adGroup/edit/:adgroup_id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/editAdGroup')),
            'editAdGroup'
          )
      },
      {
        path: 'adGroup/copy/:adgroup_id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/newAdGroup')),
            'newAdGroup'
          )
      },
      {
        path: 'ad',
        component: r =>
          require.ensure([], () => r(require('../page/advertising/ad')), 'ad')
      },
      {
        path: 'ad/new',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/newAd')),
            'newAd'
          )
      },
      {
        path: 'ad/edit/:ad_id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/editAd')),
            'editAd'
          )
      },
      {
        path: 'ad/copy/:ad_id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/newAd')),
            'newAd'
          )
      },
      {
        path: 'adCreative',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/adCreative')),
            'adCreative'
          )
      },
      {
        path: 'adCreative/new',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/newAdCreative')),
            'newAdCreative'
          )
      },
      {
        path: 'adCreative/copy/:id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/newAdCreative')),
            'newAdCreative'
          )
      },
      {
        path: 'adCreative/edit/:id',
        component: r =>
          require.ensure(
            [],
            () => r(require('../page/advertising/editAdCreative')),
            'editAdCreative'
          )
      }
    ],
    beforeRouteUpdate(to, from, next) {
      console.log('to:', to)
      console.log('from:', from)
      // do something
      next()
    }
  }
]

export default advertising



