import Vue from 'vue'
import Vuex from 'vuex'
import axios from '../util/ajax'
import getters from './getter'
import actions from './actions'

Vue.use(Vuex)

/* 全局的状态（数据源） */
const state = {
  //  账户页面弹窗
  accountPage: {
    dialogFormVisible: false,
    row: {}
  },
  user: {
    account: {}
  }
}
/* 状态的提交--变更状态 */
const mutations = {
  // 修改页面状态
  SET_ACCOUNTPAGE(state, params) {
    this.state.accountPage = params
    console.log(params)
  },
  // 修改账户
  SET_ACCOUNT(state, params) {
    this.state.user.account = params
  }
}

export default new Vuex.Store({
  state,
  mutations,
  getters,
  actions
})



