<template>
  <div class="tab-container-wrap">
    <div class="tab-title-wrap">
      <el-tabs v-model="tabItems.activeName" @tab-click="handleClick">
        <el-tab-pane v-for="(item, index) in tabItems.items" :key="index" :label="item.label" :name="item.name">
        </el-tab-pane>
      </el-tabs>
      <a v-if="isAdvertising" href="/marketing/#/newAdvertising" class="new">新建</a>
    </div>
    <div class="tab-content-wrap">
      <router-view></router-view>
    </div>
  </div>

</template>

<script>
export default {
  created() {
    if (this.tabItems.tabHost.indexOf('advertising') != -1) {
      this.isAdvertising = true
    }
  },
  // 组件
  components: {},
  data() {
    return {
      isAdvertising: false
    }
  },
  props: ['tabItems'],
  methods: {
    // 切换 顶部 tab
    handleClick(tab, event) {
      if (tab.active) {
        window.location.href = this.tabItems.tabHost + tab.name
      }
    }
  },
  computed: {
    tabActiveName: function() {}
  }
}
</script>


<style scoped>
.tab-container-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.tab-title-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  box-sizing: border-box;
}
.tab-content-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 42px;
  right: 0;
  bottom: 0;
  width: 100%;
  overflow: auto;
}

.new {
  position: absolute;
  z-index: 22;
  right: 0;
  top: 0;
  height: 40px;
  line-height: 40px;
  color: #fff;
  background: #409eff;
  text-align: center;
  padding: 0 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 14px;
  min-width: 80px;
}
</style>



