<template>
  <div class="container">
    <!-- top begin  -->
    <Navigation></Navigation>
    <!-- top end    -->

    <!-- left begin  -->
    <Aside></Aside>
    <!-- left end   -->

    <!-- main begin  -->
    <div class="main">
      <router-view/>
    </div>
    <!-- main end    -->
  </div>
</template>

<script>
import Aside from './layouts/Aside'
import Navigation from './layouts/Navigation'
export default {
  // 组件
  components: {
    Aside,
    Navigation
  }
}
</script>


<style scoped>
.container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 2;
}
.container .main {
  position: absolute;
  left: 60px;
  top: 60px;
  right: 0;
  bottom: 0;
  overflow-x: auto;
  overflow-y: auto;
}
</style>



