import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/components/Layout'
import advertising from './advertising'
import setting from './setting'
import report from './report'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      children: [
        {
          path: '/',
          component: r =>
            require.ensure([], () => r(require('../page/home/home')), 'home')
        }
      ]
    },
    {
      path: '/home',
      component: Layout,
      children: [
        {
          path: '/',
          component: r =>
            require.ensure([], () => r(require('../page/home/home')), 'index')
        }
      ]
    },
    {
      path: '/newAdvertising',
      component: Layout,
      children: [
        {
          path: '/',
          component: r =>
            require.ensure(
              [],
              () => r(require('../page/advertising/new')),
              'newAdvertising'
            )
        }
      ]
    },
    {
      path: '/advertising',
      component: Layout,
      children: advertising
    },
    {
      path: '/setting',
      component: Layout,
      children: setting
    },
    {
      path: '/report',
      component: Layout,
      children: report
    }
  ]
})



