<template>
    <TabNavigation :tabItems="tabItems"></TabNavigation>
</template>

<script>
import TabNavigation from '../../components/TabNavigation'
export default {
  // 组件
  components: {
    TabNavigation
  },
  data() {
    return {
      tabItems: {
        activeName: 'dataReport',
        tabHost: '/marketing/#/report/',
        items: [
          { label: '效果数据', name: 'dataReport' },
          // { label: '人群分析', name: 'peopleAnalysis' },
        ]
      }
    }
  },
  created: function() {
    console.log('created')
    //  this.getAd_Campaigns()
  }
}
</script>


<style scoped>
.tab-container-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.tab-title-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  box-sizing: border-box;
}
.tab-content-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 42px;
  right: 0;
  bottom: 0;
  width: 100%;
}
</style>



